@use "global" as *;


.post {
  display: block;
}

.post:hover .post__eye-catch img {  // hover
  transform: scale(1.1);
}

.post__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: rem(16);
}

.post__eye-catch {
  overflow: hidden;
}

.post__eye-catch img {
  aspect-ratio: 1 / 1;
  max-width: rem(222);
  width: 100%;
  object-fit: cover;

  transition: transform 0.2s ease-out;
}

.post__contents {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: rem(14);
}

.post__top {
  position: relative;
  width: 100%;
}

.post__category {

}

.post__glass-icon {
  display: grid;
  place-items: center;
}

.post__glass-icon svg {
  max-width: rem(35);
  width: 100%;
  position: absolute;
  top: rem(-8);
  right: 0;
}

.post__bottom {

}

.post__text {
  leading-trim: both;
  text-edge: cap;
  text-overflow: ellipsis;
  font-size: rem(14);
  font-weight: $bold;
  line-height: 1.4;
}
